#who {
    padding-top: 100px;
    justify-content: center;
    text-align: left;
    align-items: center;
    .who-wrapper {
      display: flex;
      flex-direction: row;
      justify-content: center;
      padding: 0 50px;
      .left {
        overflow: hidden;
        img {
          overflow: hidden;
          height: 725px;
          width: 655px;
          transition: transform 1s;
          box-shadow: 2px 2px 4px rgba(0, 0, 0, 0.4);
          &:hover {
            transform: scale(1.3);
          }
        }
      }
      .right {
        margin-left: 30px;
        .subtitle {
          margin-top: 30px;
          h5 {
            color: #757575;
            font-weight: 900;
            font-size: 17px;
            line-height: 24px;
          }
        }
        .title {
          margin-top: 20px;
          width: 655px;
          h2 {
            font-size: 45px;
            font-weight: 900;
            line-height: 67.5px;
            letter-spacing: 7%;
          }
        }
        .desc {
          margin-top: 15px;
          width: 655px;
          p {
            font-weight: 400;
            font-size: 17px;
            line-height: 25.5px;
            color: #6e6e6e;
          }
        }
        .skills {
          display: flex;
          flex-direction: row;
          align-items: center;
          justify-content: space-around;
          margin-top: 30px;
          p {
            display: flex;
            flex-direction: row;
            align-items: center;
            padding: 0 20px;
            color: #6e6e6e;
            font-weight: 400;
            line-height: 24px;
            font-size: 17px;
          }
        }
        .statistic {
          display: flex;
          flex-direction: row;
          align-items: center;
          justify-content: space-around;
          margin-top: 30px;
          .experience,
          .team,
          .clients {
            display: flex;
            flex-direction: column;
            align-items: center;
            margin-top: 20px;
            span {
              font-size: 45px;
              line-height: 67.5px;
              font-weight: 900;
              margin-bottom: 5px;
            }
            p {
              font-size: 15px;
              line-height: 20px;
              font-weight: 500;
              margin-top: 5px;
            }
          }
        }
      }
    }
  }
  